header {
  background: #fff;
}
header .header-inner {
  min-height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .header-inner__logo {
  font-weight: 900;
  text-transform: uppercase;
}

body {
  background: url(../media/paper-texture.jpg) center center no-repeat;
  background-size: cover;
  font-family: "Montserrat", sans-serif;
}

img {
  max-width: 100%;
}

a {
  color: #f50014;
  text-decoration: none;
}
a:link, a:active {
  color: #f50014;
  text-decoration: none;
}
a:hover {
  color: #f50014;
}

.button {
  background-color: #f50014;
  color: #fff;
  border: none;
  border-radius: 1px;
  padding: 12px 40px;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.button:hover {
  background-color: #c20010;
  color: #fff;
}

footer {
  padding: 50px 20px 40px;
  margin-top: -20px;
  position: relative;
  z-index: 1;
  background: #f50014;
  display: flex;
  color: #fff;
}
@media (max-width: 420px) {
  footer {
    flex-direction: column;
  }
}
footer a {
  color: #fff !important;
  margin: 0 10px;
}